import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

const White = () => (
  <StaticQuery
    query={graphql`
      query {
        whiteDevices: file(relativePath: { eq: "white-devices.png" }) {
          childImageSharp {
            fluid(maxWidth: 300) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <Img
        key={Math.floor(Math.random() * 6) + 1}
        fluid={data.whiteDevices.childImageSharp.fluid}
      />
    )}
  />
)
export default White
